import React, {Component} from 'react';
import Col from "reactstrap/es/Col";
import {API_URL} from "../services/helper";
import {Link} from "gatsby";

const PollItemRow = (props) => {
    return (
        <React.Fragment>
            {props.data.map((rowItem, key) =>
                <Col key={key} md={(key !== 0 && key !== 6)? 4: 8} className={"marg-b"}>
                    {(key !== 0 && key !== 6)?
                        <div className="list-item">
                            <Link to={"/poll_"+ rowItem.id} className="box-link">
                                <div className="img-box">
                                    <img src={API_URL + rowItem.title_bg.url} height={200} alt="img item"/>
                                </div>
                            </Link>
                            <div className="info">
                                <Link to={"/poll_"+ rowItem.id} className="box-link">
                                    <p className="title">
                                        <span className="rectangle rectangle__red">?</span>
                                        Should the Seahawks focus the offense around Russell Wilson instead of the running game?
                                    </p>
                                </Link>
                                <div className="text">
                                    Ut enim ad minima veniam, quis nostrum exercitationem ullam corporis suscipit laboriosam,
                                    nisi ut aliquid ex ea commodi consequatur?
                                </div>
                                <div className="desc d-flex align-items-center">
                                    <div className="desc-item">
                                        <span className="number">1.2k</span>
                                        <span className="text">Votes</span>
                                    </div>
                                    <div className="desc-item">
                                        <span className="number">2.7k</span>
                                        <span className="text">Repies</span>
                                    </div>
                                    <div className="desc-item">
                                        <span className="number">512</span>
                                        <span className="text">Views</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        :
                        <Link to={"/poll_"+ rowItem.id} className="box-link">
                        <div className="list-item-main">
                            <div className="img-box" style={{
                                backgroundImage: "url(" + API_URL + rowItem.title_bg.url + ")"
                            }}>
                                <div className="mask" />
                            </div>
                            <div className="info d-flex flex-column justify-content-end">
                                <p className="title">
                                    <span className="rectangle rectangle__red">?</span>
                                    Should the Seahawks focus the offense around Russell Wilson instead of the running game?
                                </p>
                                <div className="desc d-flex align-items-center">
                                    <div className="desc-item">
                                        <span className="number">1.2k</span>
                                        <span className="text">Votes</span>
                                    </div>
                                    <div className="desc-item">
                                        <span className="number">2.7k</span>
                                        <span className="text">Repies</span>
                                    </div>
                                    <div className="desc-item">
                                        <span className="number">512</span>
                                        <span className="text">Views</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Link>
                    }
                </Col>
            )}
        </React.Fragment>
    )
};

class PollItem extends Component {
    render() {
        const itemRow = this.props.chunk;
        return (
            <React.Fragment>
                {this.props.polls.map((pollRow, key) =>
                    <React.Fragment key={key}>
                        {/*{console.log(pollRow)}*/}
                        {(key <= itemRow) ?
                            <PollItemRow data={pollRow}/>
                            : ""}
                    </React.Fragment>
                )}

            </React.Fragment>
        );
    }
}

export default PollItem;



