import React, { useState} from 'react';
import Swiper from 'react-id-swiper';
import {StaticQuery, graphql} from "gatsby";
// Need to add Pagination, Navigation modules
import { Pagination, Navigation } from 'swiper/dist/js/swiper.esm'
import MediaQuery from 'react-responsive';
import "./index.scss";
import {API_URL} from "../services/helper";
import Container from "reactstrap/es/Container";


const SliderItem = (props) =>{
    const [swiper, updateSwiper] = useState(null);
    const [state, setState] = useState({sliderItem: 0, sliderCount: 0});


    // console.log(testERER);
    const goNext = () => {
        if (swiper !== null) {
            swiper.slideNext();

            setState({
                sliderItem: swiper.activeIndex + 1,
                sliderCount: swiper.slides.length
            });
        }
    };

    const goPrev = () => {
        if (swiper !== null) {
            swiper.slidePrev();
            setState({
                sliderItem: swiper.activeIndex + 1,
                sliderCount: swiper.slides.length
            });
        }
    };

    return (
        <React.Fragment>
            <Swiper {...props.param} getSwiper={updateSwiper} modules={props.modules}>
                <StaticQuery
                    query={graphql`
                        query SliderQuery{
                          sporttalk{
                            polls{
                              id
                              title
                              body
                              votes
                              title_bg{
                                id
                                name
                                url
                              }
                              tags{
                                name
                                id
                              }
                            }
                          }
                        }
                        `
                    }
                    render={data =>
                        <React.Fragment>
                            {data.sporttalk.polls.map((poll, key) =>
                                <div key={key} className="slider-item swiper-slide" style={{backgroundImage:'url(' + API_URL + poll.title_bg.url +')' }}>
                                    <div className={"wrap-content"} >
                                        <div className="container container-h-100">
                                            <div className="slider-content d-flex flex-column justify-content-end">
                                                <div className="info">
                                                    <p className="title">
                                                        <span className="rectangle rectangle__red">?</span>
                                                        {poll.title}
                                                    </p>
                                                    <div className="desc d-flex align-items-center">
                                                        <div className="desc-item">
                                                            <span className="number">1.2k</span>
                                                            <span className="text">Votes</span>
                                                        </div>
                                                        <div className="desc-item">
                                                            <span className="number">2.7k</span>
                                                            <span className="text">Repies</span>
                                                        </div>
                                                        <div className="desc-item">
                                                            <span className="number">512</span>
                                                            <span className="text">Views</span>
                                                        </div>
                                                    </div>
                                                </div>

                                            </div>
                                        </div>
                                        <div className="mask"/>
                                    </div>
                                </div>
                            )}
                        </React.Fragment>
                    }/>
            </Swiper>
            <Container style={{
                justifyContent: "flex-end",
                display: "flex",
            }}>
                <div className="slider-button d-none d-md-flex align-items-center" style={{
                    position: "absolute",
                    bottom: "0",
                    zIndex: "2",
                    float: "right",
                    minWidth: "288px",
                    minHeight: "96px",
                }}>
                    <div style={{cursor: "pointer"}} onClick={()=>{
                        goPrev();
                    }} className="arrow-link arrow-link__left">
                        <span className="icon-arrow icon-arrow__left"/>
                    </div>
                    <span className="count-slide" style={{color: "#ffffff"}}>
                        {(swiper !== null) ?
                           swiper.activeIndex + 1 : state.sliderItem
                        }
                        /
                        {(swiper !== null) ?
                            swiper.slides.length : state.sliderCount
                        }
                    </span>
                    <div style={{cursor: "pointer"}} onClick={()=>{
                        goNext();

                    }} className="arrow-link arrow-link__right">
                        <span className="icon-arrow icon-arrow__right"/>
                    </div>
                </div>
            </Container>
        </React.Fragment>

    )

}

const Slider = () => {
    const params = {
        modules: [Pagination, Navigation],
        pagination: {
            el: '.swiper-pagination',
            type: 'fraction',
        },
        slidesPerView: 1,
        allowTouchMove: false,
        // slideClass: "slider-item swiper-slide",
        containerClass: "slider intro-slider",
    };
    const params2 = {
        modules: [Pagination],
        pagination: {
            el: '.swiper-pagination',
            type: 'bullets',
            clickable: true
        },
        slidesPerView: 1,
        // slideClass: "slider-item swiper-slide",
        containerClass: "slider intro-slider",
    };

    return (
        <React.Fragment>
            <div className="intro">
                <MediaQuery query="(min-width: 768px)">
                    <SliderItem param={params} modules={[Navigation, Pagination]}/>

                </MediaQuery>
                <MediaQuery query="(max-width: 767px)">
                    <SliderItem param={params2} modules={[Pagination]}/>
                </MediaQuery>

            </div>
        </React.Fragment>
    );
};


export default Slider;
