import React, {Component} from 'react';
import {Container, Row} from "reactstrap";
import PollItem from "./pollItem";
import {Link, StaticQuery, graphql} from "gatsby";
import "./index.scss";
// import Col from "reactstrap/es/Col";

const PollsItems = () =>{
    return(
        <StaticQuery query={graphql`
             {
                  sporttalk{
                    polls{
                      id
                      title
                      body
                      votes
                      title_bg{
                        id
                        name
                        url
                      }
                      tags{
                        name
                        id
                      }
                    }
                  }
                }                                      
        `} render={ data =>
            <PollsList polls={data.sporttalk.polls}/>
        }/>
    )
};

class PollsList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            listItem: 0,
            chunk: 0,
        };
    }

    componentWillMount() {
        const chunk = function(array, size) {
            if (!array.length) {
                return [];
            }
            const head = array.slice(0, size);
            const tail = array.slice(size);

            return [head, ...chunk(tail, size)];
        };
        // const showMore = chunk(this.props.polls, 5;
        this.setState({listItem: chunk(this.props.polls, 5)});
        // console.log(chunk(this.props.polls, 5));

    }


    render() {
        return (
            <React.Fragment>
                <section className="content">
                    <Container>
                        <div className="list-news">
                            <div className="title-h">
                                <span className="color-red">Live</span> / archived
                            </div>
                            <div className="list-news-content">
                                <Row>
                                    <PollItem polls={this.state.listItem} chunk={this.state.chunk}/>
                                </Row>
                            </div>
                            {(this.props.polls.length > 8)?
                                <div className="btn-wrapp">
                                    <Link to={"/"}  className="button button__general">
                                    <span className="text">
                                        <span className="icon"/>
                                        Show more
                                    </span>
                                    </Link>
                                </div>: ''}

                        </div>
                    </Container>
                </section>

            </React.Fragment>
        );
    }
}

export default PollsItems;
